import React, {Component} from 'react';


class News extends Component{
    render(){
        return(
            <div>This is news</div>
        );
    }
}

class Comments extends Component{
    render(){
        return(
            <div>This is comments</div>
        );
    }
}


export default class App extends Component {
    render() {
        return (
            <div>
                <h1>This app!</h1>
                <News/>
                <Comments/>
            </div>
        );
    }
}

